<template>
  <view class="sport-form">
    <!-- 运动偏好 -->
    <form-section title="运动偏好（可多选）" required>
      <view class="form-tips">请选择您常进行的运动类型（最多选择5项）</view>
      <checkbox-group @change="handleSportPreference">
        <view class="grid">
          <label 
            v-for="item in sportOptions" 
            :key="item" 
            class="checkbox-item"
            :class="{ 'selection-limit': sportPreferences.length >= maxSelection }"
            :data-count="sportPreferences.includes(item) ? `${sportPreferences.indexOf(item)+1}` : ''"
          >
            <checkbox 
              :value="item" 
              :checked="sportPreferences.includes(item)" 
              color="#4CAF50"
              :disabled="sportPreferences.length >= maxSelection && !sportPreferences.includes(item)"
            />
            <text class="label">{{ item }}</text>
          </label>
          <view class="custom-input">
            <input 
              v-model="customSport" 
              placeholder="例如：攀岩、舞蹈等（输入后按完成添加）"
              @confirm="addCustomSport"
              class="input"
              :disabled="sportPreferences.length >= maxSelection"
            />
            <text class="input-tips">自定义项目添加后需手动选择</text>
            <text class="count-tips">已选 {{ sportPreferences.length }}/{{ maxSelection }} 项</text>
          </view>
        </view>
      </checkbox-group>
    </form-section>

    <!-- 不擅长运动 -->
    <form-section title="不擅长的运动（可多选）">
      <view class="form-tips">帮助我们避开您不擅长的运动方式</view>
      <checkbox-group @change="handleAvoidSports">
        <view class="grid">
          <label 
            v-for="item in sportOptions" 
            :key="item" 
            class="checkbox-item"
          >
            <checkbox :value="item" :checked="avoidSports.includes(item)" color="#4CAF50"/>
            <text class="label">{{ item }}</text>
          </label>
          <view class="custom-input">
            <input 
              v-model="customAvoid" 
              placeholder="例如：滑雪、体操等（输入后按完成添加）"
              @confirm="addCustomAvoid"
              class="input"
            />
            <text class="input-tips">自定义项目添加后需手动选择</text>
          </view>
        </view>
      </checkbox-group>
    </form-section>

    <!-- 新运动尝试意愿 -->
    <form-section title="是否愿意尝试新运动">
      <view class="form-tips">您的开放程度将影响推荐内容</view>
      <radio-group @change="handleNewSportWillingness">
        <view class="radio-group">
          <label 
            v-for="item in willingnessOptions" 
            :key="item.value" 
            class="radio-item"
          >
            <radio 
              :value="item.value" 
              :checked="newSportWillingness === item.value" 
              color="#4CAF50"
            />
            <view class="radio-label">
              <text>{{ item.label }}</text>
              <text class="radio-tips">{{ item.tips }}</text>
            </view>
          </label>
        </view>
      </radio-group>
    </form-section>

    <!-- 健身经验 -->
    <form-section title="健身经验" required>
      <view class="form-tips">请根据实际情况选择最符合的选项</view>
      <radio-group @change="handleExperience">
        <view class="radio-group">
          <label 
            v-for="item in experienceOptions" 
            :key="item.value" 
            class="radio-item"
          >
            <radio 
              :value="item.value" 
              :checked="experienceLevel === item.value" 
              color="#4CAF50"
            />
            <view class="radio-label">
              <text>{{ item.label }}</text>
              <text class="radio-tips">{{ item.tips }}</text>
            </view>
          </label>
        </view>
      </radio-group>
    </form-section>

    <!-- 运动强度偏好 -->
    <form-section title="运动强度偏好">
      <view class="intensity-tips">
        <text class="tip-title">强度说明：</text>
        <view v-for="(tip, index) in intensityTips" :key="index" class="tip-item">
          <text class="tip-level">{{ index+1 }}.{{ tip.level }}：</text>
          <text class="tip-desc">{{ tip.desc }}</text>
        </view>
      </view>
      <view class="intensity-slider">
        <slider 
          :value="intensityLevel" 
          min="1" 
          max="3" 
          step="1"
          activeColor="#4CAF50"
          block-size="28"
          @changing="handleIntensityChange"
        />
        <view class="intensity-labels">
          <text 
            v-for="(item, index) in intensityOptions" 
            :key="index" 
            :class="{ active: intensityLevel === index+1 }"
          >
            {{ item }}
          </text>
        </view>
      </view>
    </form-section>

    <!-- 空闲时间选择 -->
    <form-section title="工作日空闲时间" required>
      <view class="time-tips">
        <text>⏰ 请选择您通常有空的时间段（可多选）</text>
        <text class="time-require">至少选择1个时间段</text>
      </view>
      <time-selector :selected="workdayTime" @change="handleWorkdayTime"/>
    </form-section>

    <form-section title="周末空闲时间">
      <view class="time-tips">
        <text>⏰ 请选择您通常有空的时间段（可多选）</text>
      </view>
      <time-selector :selected="weekendTime" @change="handleWeekendTime"/>
    </form-section>

    <!-- 提交按钮 -->
    <button 
      class="submit-btn" 
      :disabled="!isFormValid" 
      @tap="handleSubmit"
    >
      提交运动偏好
    </button>

    <!-- 状态提示 -->
    <view v-if="submitStatus" :class="['status-message', submitStatus.type]">
      <text>{{ submitStatus.message }}</text>
      <text v-if="submitStatus.type === 'error'" class="retry-tips">（请检查网络后重试）</text>
    </view>
  </view>
</template>

<script>
	import TimeSelector from '@/components/TimeSelector.vue'
export default {
  data() {
    return {
      // 原有数据...新增以下数据
      maxSelection: 5,
      intensityTips: [
        { level: '轻度', desc: '适合办公室人群，每周1-2次，以放松为主' },
        { level: '中度', desc: '保持肌肉活力，每周3-4次，增强体质' },
        { level: '高强度', desc: '专业训练计划，每周5次以上，提升竞技能力' }
      ],
      willingnessOptions: [
        { value: 'yes', label: '愿意', tips: '乐于接受新运动类型' },
        { value: 'no', label: '不愿意', tips: '只想进行熟悉运动' },
        { value: 'maybe', label: '看情况', tips: '根据具体项目决定' }
      ],
      experienceOptions: [
        { value: 'beginner', label: '完全小白', tips: '从未系统健身' },
        { value: 'intermediate', label: '有基础经验', tips: '持续健身3-12个月' },
        { value: 'advanced', label: '长期健身者', tips: '系统健身1年以上' }
      ]
    }
  },
  methods: {
    addCustomSport() {
      if (this.sportPreferences.length >= this.maxSelection) {
        uni.showToast({
          title: `最多选择${this.maxSelection}项`,
          icon: 'none',
          duration: 2000
        })
        return
      }
      if (this.customSport.trim() && !this.sportPreferences.includes(this.customSport)) {
        this.sportPreferences.push(this.customSport.trim())
        this.customSport = ''
      }
    },
    // 其他方法保持不变...
  }
}
</script>

